
.CreditCardForm .form{
    max-width: 500px;
    padding: 10px 20px;
    background: #f4f7f8;
    margin: 10px auto;
    padding: 20px;
    background: #f4f7f8;
    border-radius: 8px;
    font-family: Georgia, "Times New Roman", Times, serif;
}
/* following for date time and zone */
.CreditCardForm #inline{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;

}
.CreditCardForm #tz{
    width: 21%;
    margin-left: 5px;
    margin-right: 5px;
}
.CreditCardForm #tz1{
    width: 13%;
    margin-left: 5px;
    margin-right: 5px;
}
.CreditCardForm #tz2{
    width: 21%;
    margin-left: 5px;
    margin-right: 5px;
}

.CreditCardForm #datetime{
    width: 40%;
    margin-left: 5px;
    margin-right: 5px;
}

.CreditCardForm #num{
    width: 50%;
    margin-left: 10px;
}
.CreditCardForm #labels{
    padding-top: 10px;
}

.CreditCardForm #coverPhoto{
    width: 60%;
    margin-left: 10px;
}


.CreditCardForm .form fieldset{
    border: none;
}

.CreditCardForm .form label {
    display: block;
    margin-bottom: 8px;
}
.CreditCardForm .form input[type="text"],
.CreditCardForm .form input[type="date"],
.CreditCardForm .form input[type="datetime-local"],
.CreditCardForm .form input[type="number"],
.CreditCardForm .form input[type="search"],
.CreditCardForm .form input[type="file"],
.CreditCardForm .form input[type="url"],
.CreditCardForm .form textarea,
.CreditCardForm .form select {
    font-family: Georgia, "Times New Roman", Times, serif;
    background: rgba(255,255,255,.1);
    border: none;
    border-radius: 4px;
    font-size: 16px;
    margin: 0;
    outline: 0;
    padding: 7px;
    width: 100%;
    box-sizing: border-box; 
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box; 
    background-color: #e8eeef;
    color:#8a97a0;
    -webkit-box-shadow: 0 1px 0 rgba(0,0,0,0.03) inset;
    box-shadow: 0 1px 0 rgba(0,0,0,0.03) inset;
    margin-bottom: 30px;
    
}
.CreditCardForm .form input[type="text"]:focus,
.CreditCardForm .form input[type="datetime-local"]:focus,
.CreditCardForm .form input[type="number"]:focus,
.CreditCardForm .form input[type="search"]:focus,
.CreditCardForm .form input[type="file"]:focus,
.CreditCardForm .form input[type="url"]:focus,
.CreditCardForm .form textarea:focus,
.CreditCardForm .form select:focus{
    background: #d2d9dd;
}
.CreditCardForm .form select{
    -webkit-appearance: menulist-button;
    height:35px;
}
.CreditCardForm .form .number {
    background: #1abc9c;
    color: #fff;
    height: 30px;
    width: 30px;
    display: inline-block;
    font-size: 0.8em;
    margin-right: 4px;
    line-height: 30px;
    text-align: center;
    text-shadow: 0 1px 0 rgba(255,255,255,0.2);
    border-radius: 15px 15px 15px 0px;
}

.CreditCardForm .form input[type="submit"],
.CreditCardForm .form button
{
    position: relative;
    display: block;
    padding: 19px 39px 18px 39px;
    color: #FFF;
    margin: 0 auto;
    background: #1abc9c ;
    font-size: 18px;
    text-align: center;
    font-style: normal;
    width: 100%;
    border: 1px solid #16a085 ;
    border-width: 1px 1px 3px;
    margin-bottom: 10px;
}
.CreditCardForm .form input[type="submit"]:hover,
.CreditCardForm .form input[type="button"]:hover
{
    background: #109177;
}



